.led-light {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  transition: all 0.3s ease;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}

.led-light-off {
  box-shadow: 0 0 10px #6b7280; /* Dim gray glow */
}

.tag-button-selected {
  box-shadow: 0 4px 14px 0 rgba(0, 118, 255, 0.39); /* Bright blue glow */
}

.tag-button-deselected {
  box-shadow: 0 4px 6px 0 rgba(0, 0, 0, 0.1); /* Subtle shadow */
}
